// frontend/src/app/questionarios/page.tsx
'use client';

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import AdminAuthGuard from '../../components/auth/AdminAuthGuard';
import api from "@/services/api";
import Link from "next/link";
import "../globals.css";
import "../questionario.css";

// --- Imports para Drag and Drop e Ícones ---
import { MoreVertical, Trash2, Edit3, Info, PlusIcon, GripVertical } from 'lucide-react';
import {
  DndContext,
  closestCenter,
  PointerSensor,
  useSensor,
  useSensors,
  DragEndEvent,
} from '@dnd-kit/core';
import {
  arrayMove,
  SortableContext,
  verticalListSortingStrategy,
} from '@dnd-kit/sortable';
import { SortableItem } from '../../components/SortableItem'; // Ajuste o caminho se necessário


// --- INTERFACES ---
interface OpcaoInterface {
  id: number;
  texto: string;
}
interface PerguntaDetalhadaInterface {
  id: number;
  enunciado: string;
  tipos: 'TEXTO' | 'MULTIPLA_ESCOLHA';
  opcoes: OpcaoInterface[];
}
interface QuePergItemInterface {
  pergunta: PerguntaDetalhadaInterface;
}
interface QuestionarioInterface {
  id: number;
  titulo: string;
  ordem: number;
  created_at: string;
  updated_at: string;
  perguntas: QuePergItemInterface[];
  criador?: { nome: string; email: string };
  _count?: { avaliacoes: number };
}

// --- Componente Principal da Página ---
function ListQuestionariosContent() {
  const router = useRouter();
  const [questionarios, setQuestionarios] = useState<QuestionarioInterface[]>([]);
  const [menuAberto, setMenuAberto] = useState<number | null>(null);
  const [detalhesVisiveis, setDetalhesVisiveis] = useState<number | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [orderChanged, setOrderChanged] = useState(false);

  // Configuração do sensor de arrastar
  const sensors = useSensors(useSensor(PointerSensor));

  useEffect(() => {
    setIsLoading(true);
    setError(null);
    api.get("/questionarios")
      .then(response => {
        // Ordena os dados que vêm do backend pelo campo 'ordem'
        const sortedData = response.data.sort((a: QuestionarioInterface, b: QuestionarioInterface) => a.ordem - b.ordem);
        setQuestionarios(sortedData);
      })
      .catch(err => {
        console.error("Erro ao buscar questionários:", err);
        setError("Erro ao buscar questionários.");
      })
      .finally(() => setIsLoading(false));
  }, []);

  // Função chamada quando o usuário solta um card
  const handleDragEnd = (event: DragEndEvent) => {
    const { active, over } = event;
    if (over && active.id !== over.id) {
      setQuestionarios((items) => {
        const oldIndex = items.findIndex((item) => item.id === active.id);
        const newIndex = items.findIndex((item) => item.id === over.id);
        setOrderChanged(true); // Mostra o botão "Salvar Ordem"
        return arrayMove(items, oldIndex, newIndex);
      });
    }
  };

  // Função para enviar a nova ordem para o backend
  const handleSaveOrder = async () => {
    setIsLoading(true);
    const orderedIds = questionarios.map(q => q.id);
    try {
      await api.patch('/questionarios/reorder', { orderedIds });
      setOrderChanged(false); // Esconde o botão após salvar
      alert('Ordem salva com sucesso!');
    } catch (err) {
      console.error("Erro ao salvar a ordem:", err);
      alert('Falha ao salvar a nova ordem.');
      setError('Falha ao salvar a nova ordem.');
    } finally {
      setIsLoading(false);
    }
  };

  // Suas funções existentes
  const formatDate = (isoDate: string) => {
    if (!isoDate) return "Data indisponível";
    return new Date(isoDate).toLocaleString("pt-BR", {
      day: "2-digit", month: "2-digit", year: "numeric",
      hour: "2-digit", minute: "2-digit",
    });
  };

  const handleDeleteQuestionario = async (id: number) => {
    if (!window.confirm("Deseja realmente excluir este questionário?")) return;
    setIsLoading(true);
    try {
      await api.delete(`/questionarios/${id}`);
      setQuestionarios((prev) => prev.filter(q => q.id !== id));
      alert('Questionário excluído com sucesso!');
    } catch (err) {
      console.error("Erro ao excluir questionário:", err);
      alert('Falha ao excluir questionário.');
      setError('Falha ao excluir questionário.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditQuestionario = (id: number) => {
    router.push(`/questionarios/${id}`);
  };

  const toggleMenu = (id: number, event?: React.MouseEvent) => {
    event?.stopPropagation();
    setMenuAberto(prev => (prev === id ? null : id));
  };

  if (isLoading) {
    return (
      <div className="flex justify-center items-center h-40">
        <span>Carregando...</span>
      </div>
    );
  }
  if (error) {
    return (
      <div className="flex justify-center items-center h-40 text-red-500">
        <span>{error}</span>
      </div>
    );
  }

  return (
    <div className="page-container">
      <div className="table-header-actions">
        <h3 className="text-xl sm:text-2xl font-semibold text-foreground">
          Meus Questionários
        </h3>
        <div className="button-group flex space-x-2">
          {/* Botão para salvar a ordem, aparece quando necessário */}
          {orderChanged && (
            <button onClick={handleSaveOrder} className="btn btn-secondary" disabled={isLoading}>
              {isLoading ? 'Salvando...' : 'Salvar Nova Ordem'}
            </button>
          )}
          <Link href="/questionarios/create" className="btn btn-primary">
            <PlusIcon className="-ml-1 mr-2 h-5 w-5" />
            Novo Questionário
          </Link>
        </div>
      </div>

      {questionarios.length === 0 ? (
        <div className="flex justify-center items-center h-40">
          <span>Nenhum questionário encontrado.</span>
        </div>
      ) : (
        <DndContext sensors={sensors} collisionDetection={closestCenter} onDragEnd={handleDragEnd}>
          <SortableContext items={questionarios.map(q => q.id)} strategy={verticalListSortingStrategy}>
            <div className="questionarios-grid">
              {questionarios.map((q) => (
                <SortableItem key={q.id} id={q.id}>
                  <div
                    className="questionario-card"
                    role="button"
                    tabIndex={0}
                    onClick={() => handleEditQuestionario(q.id)}
                    onKeyDown={(e) => { if (e.key === 'Enter' || e.key === ' ') handleEditQuestionario(q.id); }}
                  >
                    <div className="card-header">
                      <h4>{q.titulo}</h4>
                      <button
                        className="botao icon-button"
                        title="Opções"
                        onClick={(e) => toggleMenu(q.id, e)}
                        aria-label="Opções do questionário"
                      >
                        <MoreVertical size={20} />
                      </button>
                    </div>
                    <div className="perguntas-list">
                      {q.perguntas && q.perguntas.length > 0 ? (
                        <ul>
                          {q.perguntas.map((p, idx) => (
                            <li key={p.pergunta.id}>
                              {idx + 1}. {p.pergunta.enunciado}
                            </li>
                          ))}
                        </ul>
                      ) : (
                        <span className="text-xs text-gray-400">Sem perguntas</span>
                      )}
                    </div>
                    {menuAberto === q.id && (
                      <div className="menu-dropdown" onClick={(e) => e.stopPropagation()}>
                        <button className="dropdown-item" onClick={() => handleEditQuestionario(q.id)}>
                          <Edit3 size={16} /> Editar
                        </button>
                        <button className="dropdown-item" onClick={() => handleDeleteQuestionario(q.id)}>
                          <Trash2 size={16} /> Excluir
                        </button>
                        <button className="dropdown-item" onClick={() => setDetalhesVisiveis(q.id)}>
                          <Info size={16} /> Detalhes
                        </button>
                      </div>
                    )}
                  </div>
                </SortableItem>
              ))}
            </div>
          </SortableContext>
        </DndContext>
      )}
    </div>
  );
}

export default function ProtectedListQuestionariosPage() {
  return (
    <AdminAuthGuard>
      <ListQuestionariosContent />
    </AdminAuthGuard>
  );
}